import { Pipe, PipeTransform } from '@angular/core';
import { AccountType } from '../../core/enums/account-type.enum';
@Pipe({
  name: 'accountTypePipe'
})
export class AccountTypePipe implements PipeTransform {

  /** TODO: stringnumber */
  transform(type: string | number): string {
    const typeMap: any = {
      [AccountType.new]: '',
      [AccountType.newRecruit]: '',
      [AccountType.sales]: '',
    };
    return typeMap[type] ? typeMap[type] : '';
  }

}
