export class PageSet {
  /**  */
  totalCounts = 0;
  /**  */
  private pageIndex = 1;
  /**  */
  private pageSize = 10;

  constructor() {

  }

  initialize(totalCounts: number): void {
    this.pageIndex = 1;
    this.pageSize = 10;
    this.totalCounts = totalCounts;
  }

  /**  */
  get totalPages(): number {
    return Math.ceil(this.totalCounts / this.pageSize) === 0 ? 1 : Math.ceil(this.totalCounts / this.pageSize);
  }

  get currentPage(): number {
    return this.pageIndex;
  }

  set currentPage(index: number) {
    this.pageIndex = index;
  }

  get currentPageSize(): number {
    return this.pageSize;
  }

  set currentPageSize(size: number) {
    this.pageIndex = 1;
    this.pageSize = size;
  }


}
