import { SignupErrorModalComponent } from './../signup-error-modal/signup-error-modal.component';
import { errorMsg, MessageService } from 'merc-lib';
import { SignUpStatus } from '../../../core/enums/signup-status.enum';
import { NzModalRef, NzModalService } from 'ng-zorro-antd/modal';
import { ICON_PATH } from '../../../content/const/icon-path.const';
import { Component, Input, OnInit } from '@angular/core';
import { REGISTER_TYPE } from '../../../content/const/register-type.const';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { UploadCreditFileComponent } from './content/upload-credit-file/upload-credit-file.component';
import { OutsideExamType, Track } from '../../../content/quiz/enums/quiz.enum';

@Component({
  selector: 'app-singup-modal',
  templateUrl: './signup-modal.component.html',
  styleUrls: ['./signup-modal.component.scss']
})
export class SignupModalComponent implements OnInit {
  /**  or  */
  @Input() action!: number;
  /**  */
  @Input() event: any;
  /**  or  */
  @Input() type = REGISTER_TYPE.COURSE;
  /**  */
  @Input() outsideExam!: OutsideExamType;
  OutsideExamType = OutsideExamType;
  get ICON_PATH(): typeof ICON_PATH {
    return ICON_PATH;
  }
  REGISTER = REGISTER_TYPE;

  /**  or  */
  typeWording!: string;

  /** enum */
  SignUpStatus = SignUpStatus;

  /**  */
  validateForm!: FormGroup;
  /**  */
  isTrack2 = false;
  Track = Track;
  errorMsg = errorMsg;
  constructor(
    public modalRef: NzModalRef,
    private messageService: MessageService,
    private modalService: NzModalService,
    private fb: FormBuilder,
  ) {
  }

  ngOnInit(): void {
    if (this.type === REGISTER_TYPE.COURSE) {
      this.typeWording = '';
    } else {
      this.typeWording = '';
    }

    this.event = { subject: ' - ', location: ' - ', date: '110/03/04110/03/05', time: '09:0018:00' };
    this.initForm();
    if (this.type === this.REGISTER.OUTSIDE_EXAM) {
      if (this.outsideExam === OutsideExamType.invest) {
        this.validateForm.get('track')?.enable();
      }
      if (this.action === SignUpStatus.signed) {
        this.validateForm.disable();
      }
    }
  }

  private initForm(): void {
    this.validateForm = this.fb.group({
      identity: ['', Validators.required],
      track: [{ value: null, disabled: true }, Validators.required]
    });
  }
  private isFormValidate(): boolean {
    for (const i in this.validateForm.controls) {
      if (i) {
        this.validateForm.controls[i].markAsDirty();
        this.validateForm.controls[i].updateValueAndValidity();
      }
    }
    return this.validateForm.valid;
  }

  onSendClick(): void {
    // if (!this.isFormValidate()) { return; }

    this.modalRef.triggerOk();
    const isSuccess = Math.random() * 10 > 5;
    switch (this.action) {
      // 
      case SignUpStatus.isOpen:
        if (isSuccess) {
          this.messageService.successModal(`/
          /`, { imgPath: './assets/image/icon/icon_success@3x.png', msgTitle: '' });
        } else {
          this.modalService.create({
            nzContent: SignupErrorModalComponent,
            nzWidth: 400,
            nzMaskClosable: false,
            nzFooter: null,
            nzBodyStyle: { padding: '12px 24px' },
          });
        }
        break;
      // 
      case SignUpStatus.signed:
        if (isSuccess) {
          this.messageService.successModal(`
          `, { imgPath: './assets/image/icon/icon_success@3x.png', msgTitle: '' });
        } else {
          this.messageService.errorModal(`
          /`, { imgPath: './assets/image/icon/icon_error@2x.png', msgTitle: '' });
        }
        break;
      default:
        console.log('signup modal nothing happened');
    }

  }

  onDownloadLinkClick(): void {
    let url = '';
    if (this.outsideExam === OutsideExamType.professional || this.outsideExam === OutsideExamType.common) {
      url = 'https://www.mli.com.tw/aes/winz/p-trainning-5.pdf';
    } else if (this.outsideExam === OutsideExamType.invest) {
      url = 'https://www.mli.com.tw/aes/winz/p-trainning-6.pdf';
    }
    window.open(url, '_blank');
  }

  onTrackChange(track: Track): void {
    if (track === Track.track2) {
      this.isTrack2 = true;
    } else {
      this.isTrack2 = false;
    }
  }
  onNextClick(): void {
    if (!this.isFormValidate()) { return; }
    this.modalService.create({
      nzContent: UploadCreditFileComponent,
      nzWidth: 600,
      nzMaskClosable: false,
      nzFooter: null,
      nzBodyStyle: { padding: '12px 24px' },
      nzComponentParams: {

      }
    });
  }
}
